<template>
    <el-card class="header">
        <div class="le">
            <h2>请开始一天的教学吧。</h2>

            <p> 课程数量 </p>
            <p> 创建题目数量 </p>
            <p> 未完成作业数量 </p>
        </div>
        <div class="ri">
            <img class="ri-img" decoding="async" src="https://demo.gin-vue-admin.com/assets/dashboard.70e55b71.png" alt>
        </div>
    </el-card>


    <el-card>
        <div>
            <div id="main" style="width: 600px;height:300px; float: left;"></div>
            <div id="main2" style="width: 600px;height:300px; float: right;"></div>
        </div>

    </el-card>
</template>

<script>

export default {

    data() {

        return {
            // 控制收缩
            isCollapse: false,

        }
    },
    methods: {
        leftChart() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("main"));
            // 指定图表的配置项和数据
            let option = {
                title: {
                    text: "课程作业重复率排行"
                },
                tooltip: {},
                legend: {
                    data: ["重复率"]
                },
                xAxis: {
                    data: ["软件1902", "软件1901", "软件1905", "计算机1901", "计算机1903"]
                },
                yAxis: {},
                series: [
                    {
                        name: "重复率",
                        type: "bar",
                        data: [36, 31, 29, 20, 14, 13]
                    }
                ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
        },
        rightChart() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("main2"));
            // 指定图表的配置项和数据
            let option = {
                title: {
                    text: "课程作业完成率排行"
                },
                tooltip: {},
                legend: {
                    data: ["完成率"]
                },
                xAxis: {
                    data: ["软件1902", "软件1901", "软件1905", "计算机1901", "计算机1903"]
                },
                yAxis: {},
                series: [
                    {
                        name: "完成率",
                        type: "bar",
                        data: [90, 86, 77, 75, 75, 74]
                    }
                ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
        }
    },
    mounted() {
        this.leftChart();
        this.rightChart();

    }
};
</script>
<style>
.el-row {
    margin-bottom: 20px;
}


.box-card {
    height: calc(100vh/4);
}

.card2 {
    height: calc(100vh/3);
}

.le {

    float: left;
}

.ri {
    max-height: 280px;
    float: right;
}

.ri-img {
    height: 600px;
    width: 600px;
    margin-top: -120px;
}

.base {
    width: 100%;
    /* 宽度设置为 100% */
    height: calc(100vh - 16px);
    /* 高度设置为视口高度 */
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

.header {
    margin-bottom: 20px;
}
</style>
  
